import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday } from "../../GlobalComponent/function";
import { getUserID } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const PrintNotaLevel2 = (
  row1name = "",
  row1isi = "",
  row2name = "",
  row2isi = "",
  row3name = "",
  row3isi = "",
  row4name = "",
  row4isi = "",
  username = "",
  tanggal = "",
  validby = "",
  tableColumn = [],
  title = "",
  tableRows,
  footer = []
) => {
  // initialize jsPDF
  const doc = new jsPDF("portrait");
  doc.setProperties({
    title: title,
  });
  const logo =
    "data:image/png;base64,";

  footer.length !== 0
    ? doc.autoTable({
        head: tableColumn,
        body: tableRows,
        foot: [footer],
        startY: 50,
        autoSize : true,
        theme: "plain",
        rowPageBreak: "avoid",
        pageBreak: "avoid",
        margin: { top: 20 },
        bodyStyles: { lineColor: "#969696",fontSize : 8, lineWidth: 0.05},
        headStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fontSize : 8,
          fillColor: "#E8E8E8",
        },
        footStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fillColor: "#E8E8E8",fontSize : 8,
        },
      })
    : doc.autoTable({
        head: tableColumn,
        body: tableRows,
        foot: [footer],
        autoSize : true,
        startY: 55,
        theme: "plain",
        rowPageBreak: "avoid",
        pageBreak: "avoid",
        margin: { top: 20 },
        bodyStyles: { lineColor: "#969696",fontSize : 8, lineWidth: 0.05},
        headStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fontSize : 8,
          fillColor: "#E8E8E8",
        },
      });
  let finalY = doc.lastAutoTable.finalY + 10;
  doc.setFontSize(12);
  doc.addImage(logo, "PNG", 14, 15, 35, 12);
  doc.text(title, 14, 32);
  //row 1
  doc.setFontSize(8);
  doc.text(row1name, 14, 37);
  doc.text(row1isi === "" ? "" : ":", 37, 37);
  doc.text(row1isi, 40, 37);
  doc.text(row2name, 120, 37);
  doc.text(row2isi === "" ? "" : ":", 141, 37);
  doc.text(row2isi, 145, 37);
  //row 2
  doc.text(row3name, 14, 42);
  doc.text(row3isi === "" ? "" : ":", 37, 42);
  doc.text(row3isi, 40, 42);
  doc.text(row4name, 120, 42);
  doc.text(row4isi === "" ? "" : ":", 141, 42);
  doc.text(row4isi, 145, 42);
  // doc.fontSize(5)
  doc.text("( LAPIS II ) / ", 145, 45);
  doc.text("( LAPIS I )", 163, 45);
  // doc.fontSize(8)
  doc.text("Penerima", 25, finalY + 60);
  doc.text("Pengirim", 95, finalY + 60);
  doc.text("Mengetahui", 160, finalY + 60);
  //Stamp
  doc.text("User", 14, finalY + 10);
  doc.text(":", 30, finalY + 10);
  doc.text(getUserID(), 33, finalY + 10);
  doc.text("Cetak", 14, finalY + 15);
  doc.text(":", 30, finalY + 15);
  doc.text(getToday(), 33, finalY + 15);
  // doc.text("Valid", 14, finalY + 20);
  // doc.text(":", 30, finalY + 20);
  // doc.text(validby, 33, finalY + 20);
  // doc.autoPrint();
  // doc.save(`${title}_${dateStr}.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>" +
      title +
      "</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  x.document.close();
};

export default PrintNotaLevel2;
