import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { renderField, renderFieldSelect } from "../../renderField";
import Skeleton from "react-loading-skeleton";
import { getListBarang } from "../../actions/kodeBarangAction";
import { setFocus } from "../../helper";
import { AxiosMasterGet } from "../../Axios";
import { warning } from "../../Alert";
import { getListPOPenjualan } from "../../actions/validasiPOAction";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { getToday } from "../../GlobalComponent/function";
import { setTambahBarangPO } from "../../actions/preOrder";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormPOCustomerNew extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kodeBarang: [],
      non_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
      tambahBarang: "col-lg-12 d-none",
      tabel: "col-lg-12",
      columns_barang: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "nama_jenis",
          text: "Nama Model",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => {
                    this.pilihBarang(row.kode_jenis);
                    this.props.dispatch(setTambahBarangPO(row.kode_jenis));
                    this.setState({
                      tambahBarang: "col-lg-12",
                      tabel: "col-lg-12 d-none",
                    });
                  }}
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_preorder",
          text: "Nomor PO",
        },
        {
          dataField: "kode_lokasi",
          text: "Lokasi",
        },
        {
          dataField: "kode_sales",
          text: "Sales",
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    this.props.dispatch(getListPOPenjualan());
    this.props.dispatch(getListBarang());
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
    this.props.change("discGlobalGr", this.props.discGlobalGr);
    this.props.change(
      "nettoDiscGlobalExt",
      (this.props.netto - this.props.discGlobalGr).toFixed(3)
    );
  }

  pilihBarang(data) {
    let param = data;
    let hasil = this.props.KodeBarangList.filter(
      (data) => data.kode_jenis === param
    );
    let customer = localStorage.getItem("customer_1") || "-";
    AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${customer}&${data}&${
        this.props.lokasi === "SALES" ? this.props.sales : this.props.lokasi
      }`
    ).then((res) => {
      if (res.data?.total_netto < 1) {
        warning("Stock Barang Kurang Dari 1");
        this.props.change("kodeBarang");
        return false;
      }
      if (
        res.data?.kode_tukar_normal === undefined ||
        res.data?.kode_tukar_normal === "undefined"
      ) {
        warning(
          "Kode Tukar Belum Ditentukan ,Mohon tentukan Kode Tukar di menu kelompok harga"
        );
        this.props.change("kodeBarang");
        return false;
      }
      if (res.data?.kode_kelompok === "AKSESORIS") {
        this.setState({
          non_aksesoris: "col-lg-12 row d-none",
          aksesoris: "col-lg-12 row",
        });
      } else {
        this.setState({
          non_aksesoris: "col-lg-12 row",
          aksesoris: "col-lg-12 row d-none",
        });
      }
      this.setState({
        kodeJenis: hasil[0].kode_jenis,
        namaJenis: hasil[0].nama_jenis,
        kadarCetak: res.data?.kode_tukar_normal,
        kadarJual: res.data?.kode_tukar_normal,
        kodeKategori: res.data?.kode_kategori,
        diskon: res.data?.diskon || 0,
        namaKategori: res.data?.nama_kategori,
        harga_jual: res.data?.harga_aksesoris || 0,
      });
      this.setKadar();
      setFocus("Plastik");
    });
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
    this.props.change("kodeKategori", this.state.kodeKategori);
    this.props.change("namaKategori", this.state.namaKategori);
    this.props.change("harga_jual", this.state.harga_jual);
    this.props.change("discGlobalPr", this.state.diskon);
  }

  setBruto() {
    this.props.change("bruto", localStorage.getItem("Timbangan"));
  }

  render() {
    const expandRow = {
      onlyOneExpanding: true,
      renderer: (row) => (
        <div>
          <GlobalTabel
            keyField="kode_jenis"
            columns={this.state.columns_barang}
            data={row.detail_barang}
          />
        </div>
      ),
    };
    return (
      <>
        <div className={this.state.tabel}>
          <GlobalTabel
            keyField="no_preorder"
            data={this.props.listPO.filter(
              (data) =>
                data.tanggal === getToday() &&
                data.kode_customer === this.props.kode_customer
            )}
            columns={this.state.columns}
            textEmpty="Data Timbangan Kosong"
            expandRow={expandRow}
          />
        </div>
        <div className={this.state.tambahBarang}>
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-md-12 col-lg-12 d-none">
                <Field
                  name="noFaktur"
                  label={this.props.noFaktur}
                  type="text"
                  readOnly={true}
                  component={renderField}
                  defaultValue={this.props.noFaktur}
                  normalize={(value) => value && value.toUpperCase()}
                />
              </div>
              <div className="col-md-4 col-lg-4">
                {String(this.props.KodeBarangList) !== "" ? (
                  <Field
                    name="kodeBarang"
                    label="Kode Model"
                    component={renderFieldSelect}
                    disabled
                    className="form-control"
                    onChange={(data) => this.pilihBarang(data)}
                    options={this.props.KodeBarangList.filter(
                      (data) =>
                        data.kode_kelompok === "PERHIASAN" ||
                        data.kode_kelompok === "AKSESORIS"
                    ).map((kode) => {
                      let data = {
                        value: kode.kode_jenis,
                        name: kode.nama_jenis,
                      };
                      return data;
                    })}
                  />
                ) : (
                  <Skeleton width={"100%"} height={50} />
                )}
              </div>
              <div className="col-md-4 col-lg-4 d-none">
                <Field
                  name="kode_barocde"
                  label="Kode Barcode"
                  type="text"
                  component={renderField}
                  placeholder="Biarkan Kosong jika Tidak Digunakan"
                  normalize={(value) => value && value.toUpperCase()}
                />
              </div>
              <div
                className={
                  this.state.aksesoris !== "col-lg-12 row"
                    ? "col-md-3 col-lg-3 d-none"
                    : "col-md-3 col-lg-3 d-none"
                }
              >
                <Field
                  name="kadar"
                  label="Kode Tukar"
                  type="number"
                  component={renderField}
                />
              </div>
              <div className="col-md-3 col-lg-3 d-none">
                <Field
                  name="kodeKategori"
                  label="Kode Tukar"
                  type="number"
                  component={renderField}
                />
              </div>
              <div className="col-md-3 col-lg-3 d-none">
                <Field
                  name="kode_kategori"
                  label="Kode Kategori"
                  type="text"
                  component={renderField}
                />
              </div>
              <div className={this.state.non_aksesoris}>
                <div className="col-md-12 col-lg-12 d-none">
                  <Field
                    name="keterangan"
                    label="Keterangan"
                    type="text"
                    component={renderField}
                    normalize={(data) => data?.toUpperCase()}
                  />
                </div>
                <div className="col-md-3 col-lg-3 d-none">
                  <Field
                    name="namaKategori"
                    label="Kode Tukar"
                    type="number"
                    component={renderField}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="p"
                    label="Plastik"
                    type="number"
                    component={renderField}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="k"
                    label="Kertas"
                    type="number"
                    component={renderField}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="pkg"
                    label="Pkg (Gr)"
                    type="number"
                    component={renderField}
                    onBlur={() => this.onclick1()}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="bruto"
                    label="Berat (Gr)"
                    type="number"
                    component={renderField}
                    onChange={this.onclick1()}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="kadarTransaksi"
                    label="Kode Tukar"
                    type="number"
                    component={renderField}
                    onBlur={() => this.onclick1()}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="netto"
                    label="Netto (Gr)"
                    type="number"
                    component={renderField}
                    readOnly={true}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="gross"
                    label="Gross (Gr)"
                    type="number"
                    component={renderField}
                    readOnly={true}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="discGlobalPr"
                    label="Diskon (%)"
                    type="number"
                    component={renderField}
                    onBlur={() => this.onclick1()}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="discGlobalGr"
                    label="Diskon (Gr)"
                    type="number"
                    component={renderField}
                    readOnly={true}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <Field
                    name="nettoDiscGlobalExt"
                    label="Netto - Diskon (Gr)"
                    type="number"
                    component={renderField}
                    readOnly={true}
                  />
                </div>
                <div className="col-md-3 col-lg-3">
                  <label htmlFor="">Ongkos (Rp)</label>
                  <Field
                    name="rp"
                    component="input"
                    type="tel"
                    className="form-control"
                    {...currencyMask}
                  />
                </div>
              </div>
              <div className={this.state.aksesoris}>
                <div className="col-lg-3">
                  <Field
                    name="qty"
                    label="Qty"
                    type="number"
                    component={renderField}
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="harga_jual"
                    label="Harga Jual"
                    type="telp"
                    component={renderField}
                    {...currencyMask}
                  />
                </div>
              </div>
              <div className="col-lg-12 col-md-12">
                <div className="text-right">
                  <button
                    type="submit"
                    disabled={this.props.onSend}
                    className="btn btn-primary"
                  >
                    {this.props.onSend ? (
                      <span
                        className="spinner-border spinner-border-sm mr-3"
                        aria-hidden="true"
                      ></span>
                    ) : null}
                    Simpan
                    <i className="fa fa-paper-plane ml-3"></i>
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </>
    );
  }
}

FormPOCustomerNew = reduxForm({
  form: "FormPOCustomerNew",
  enableReinitialize: true,
})(FormPOCustomerNew);
const selector = formValueSelector("FormPOCustomerNew"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, pkg, discGlobalPr } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg",
    "discGlobalPr"
  );
  return {
    netto: (
      parseFloat(bruto || 0) *
      (parseFloat(kadarTransaksi || 0) / 100)
    ).toFixed(3),
    gross: parseFloat(bruto || 0) + parseFloat(pkg || 0),
    discGlobalGr: (
      parseFloat(bruto || 0) *
      (parseFloat(discGlobalPr || 0) / 100)
    ).toFixed(3),
    KodeBarangList: state.provinsi.kodeBarang,
    onSend: state.provinsi.onSend,
    listPO: state.provinsi.listPO,
    kodeBarang: state.provinsi.kodeBarang,
  };
})(FormPOCustomerNew);
